import AppModel from '../models/AppModel';
import AppView from '../views/AppView';
import SearchView from '../views/SearchView';
import Slider from '../views/Slider';

export default class App {
  constructor() {
    this.state = {
      url: 'https://www.googleapis.com/youtube/v3/search?key=AIzaSyBhMIY-aXNAC2jPwxfPttI-rMHsPtgZtMU&type=video&part=snippet&maxResults=16&q=',
      model: null,
      data: null,
      view: null,
      href: null,
    };
    this.count = null;
    this.check = 10;
    this.isDown = false;
    this.startX = null;
    this.scrollLeft = null;
    this.x = null;
    this.tmp = 1;
    this.accumScroll = null;
    this.touchPoint = null;
    this.delta = null;
    this.event = null;
    this.onresize = function () {
      const width = document.querySelector('.slider-container').clientWidth;
      const widthMaxScrollBy = -200000000;

      if (this.widthStr !== width) {
        document.querySelector('.slider').scrollBy(widthMaxScrollBy, 0);
        document.querySelector('.round').innerHTML = `${this.count = 1}`;
        this.x = null;
        this.widthStr = document.querySelector('.slider-container').clientWidth;
      }

      return width;
    };

    this.countSlideList = function (size) {
      const countSlide = {
        onePicture: 772,
        twoPicture: 1167,
        threePicture: 1545,
      };

      const { onePicture, twoPicture, threePicture } = countSlide;

      if (size < onePicture) {
        return 1;
      } if (size > onePicture && size < twoPicture) {
        return 2;
      }
      if (size > twoPicture && size < threePicture) {
        return 3;
      }
      if (size > threePicture) {
        return 4;
      }
      return null;
    };

    this.list = async function (num, flag) {
      const slideCount = document.getElementsByClassName('slide').length;
      switch (flag) {
        case 'plus':
          this.changeScreen = this.countSlideList(num);
          this.x += this.countSlideList(num);
          if (this.x < slideCount) {
            if (this.x > this.check) {
              const val = document.querySelector('input').value;
              this.state.url = `https://www.googleapis.com/youtube/v3/search?pageToken=${this.state.data[0]}&key=AIzaSyBhMIY-aXNAC2jPwxfPttI-rMHsPtgZtMU&type=video&part=snippet&maxResults=16&q=`;
              this.state.href = val;

              this.state.model = new AppModel(this.state);
              this.state.data = await this.state.model.getClipNames().catch(err => { alert(`There has been a problem with your function: ${err}`); });
              this.state.view = new AppView(this.state.data[1]);
              this.state.view.render();
              this.check = slideCount - 10;
            }
          }
          break;

        case 'minus':
          this.x -= this.countSlideList(num);
          if (this.x > this.countSlideList(num)) {
            if (this.count < 2) break;
          }
          break;

        default:
          break;
      }
      this.isDown = false;
    };
  }

  async start() {
    const search = new SearchView();
    const slider = new Slider();

    search.render();
    slider.render();

    document.querySelector('button').addEventListener('click', async () => {
      if (document.querySelector('.slide')) {
        Slider.delete();
        this.x = null;
        this.check = 10;
      }

      document.querySelector('.slider-container__button').classList.remove('slider-container__button-opacity');
      document.querySelector('.round').innerHTML = `${this.count = 1}`;

      const val = document.querySelector('input').value;
      this.state.href = val;

      this.state.model = new AppModel(this.state);
      this.state.data = await this.state.model.getClipNames().catch(err => { alert(`There has been a problem with your function: ${err}`); });
      this.state.view = new AppView(this.state.data[1]);

      document.querySelector('.slider-container__button').classList.add('slider-container__button-opacity');
      this.state.view.render();
    });

    document.querySelector('input').addEventListener('keyup', async e => {
      const { keyCode } = e;
      const upEnter = 13;

      if (keyCode === upEnter) {
        if (document.querySelector('.slide')) {
          Slider.delete();
          this.x = null;
          this.check = 10;
        }

        document.querySelector('input').blur();
        document.querySelector('.slider-container__button').classList.remove('slider-container__button-opacity');
        document.querySelector('.round').innerHTML = `${this.count = 1}`;

        const val = document.querySelector('input').value;
        this.state.href = val;

        this.state.model = new AppModel(this.state);
        this.state.data = await this.state.model.getClipNames().catch(err => { alert(`There has been a problem with your function: ${err}`); });
        this.state.view = new AppView(this.state.data[1]);

        document.querySelector('.slider-container__button').classList.add('slider-container__button-opacity');
        this.state.view.render();
      }
    });

    document.querySelector('.next-slide-clip').addEventListener('click', () => {
      const res = this.onresize() / 0.9;
      if (this.x === 'null') {
        this.x = this.countSlideList(res) + 1;
      }
      const flag = 'plus';
      this.list(res, flag);
      document.querySelector('.round').innerHTML = `${this.count += 1}`;
      document.querySelector('.slider').scrollBy(`${res}`, 0);
      this.accumScroll += res;
    });

    document.querySelector('.prev-slide-clip').addEventListener('click', () => {
      const res = this.onresize() / 0.9;
      if (this.x === 'null') {
        this.x = this.countSlideList(res) - 1;
      }
      document.querySelector('.slider').scrollBy(`-${res}`, 0);
      if (this.count > 1) {
        document.querySelector('.round').innerHTML = `${this.count -= 1}`;
      }
      const flag = 'minus';
      this.list(res, flag);
    });

    document.querySelector('.slider-container').addEventListener('mousedown', e => {
      const { pageX } = e;
      this.isDown = true;
      this.startX = pageX - document.querySelector('.slider').offsetLeft;
      this.scrollLeft = document.querySelector('.slider').scrollLeft;
      console.log(this.scrollLeft);
    });

    document.querySelector('.slider-container').addEventListener('mouseleave', () => {
      this.isDown = false;
    });

    document.querySelector('.slider-container').addEventListener('mouseup', () => {
      this.isDown = false;

      if (this.startX > (this.onresize() / 2)) {
        if (!this.event) return;
        document.querySelector('.round').innerHTML = `${this.count += 1}`;
        this.event = null;
      } else if ((this.onresize() / 2) > this.startX) {
        if (!this.event) return;
        if (this.count > 1) {
          document.querySelector('.round').innerHTML = `${this.count -= 1}`;
        }
        this.event = null;
      }
    });

    document.querySelector('.slider-container').addEventListener('mousemove', e => {
      const { pageX } = e;

      if (!this.isDown) return;
      e.preventDefault();
      this.delta = pageX - document.querySelector('.slider').offsetLeft;
      if (this.startX > this.delta) {
        const res = this.onresize() / 0.9;
        if (res) this.event = res;
        document.querySelector('.slider').scrollBy(`${res}`, 0);
        const flag = 'plus';
        this.list(res, flag);
      } else if (this.delta > this.startX) {
        const wi = getComputedStyle(document.querySelector('.slider')).width;
        const res = wi.slice(0, -2) / 0.9;
        if (res) this.event = res;
        document.querySelector('.slider').scrollBy(`${-res}`, 0);
        const flag = 'minus';
        this.list(res, flag);
      }
    });

    document.querySelector('.slider-container').addEventListener('touchstart', e => {
      if (this.onresize() < 700) {
        const touchobj = e.changedTouches[0];
        this.touchPoint = parseInt(touchobj.clientX);

        const res = this.onresize() / 0.9;
        if (this.x === null) {
          this.x = this.countSlideList(res) + 1;
        }
        const flag = 'plus';
        this.list(res, flag);
      }
    });

    document.querySelector('.slider-container').addEventListener('touchend', e => {
      const touchobj = e.changedTouches[0];
      const startx = parseInt(touchobj.clientX);

      if (this.onresize() < 700) {
        if (this.touchPoint < startx) {
          if (this.count > 1) {
            document.querySelector('.round').innerHTML = `${this.count -= 1}`;
          }
        } else if (this.touchPoint > startx) {
          document.querySelector('.round').innerHTML = `${this.count += 1}`;
        }
      }
    });

    window.addEventListener('resize', this.onresize);

    this.widthStr = document.querySelector('.slider-container').clientWidth;
  }
}
